// import styled from '@emotion/styled';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  useMediaQuery,
  useTheme,
} from '@material-ui/core';
import AutoCompleteField, { SelectOption } from 'components/form/AutoCompleteField';
import { css } from 'emotion';
import React from 'react';
import { useForm } from 'react-hook-form';
import { AccountAccessType, AccountAccessTypeLabels } from 'types/AccountAccessType';
import AccountUser from 'types/AccountUser';

const ContentStyle = css`
  width: 350px;
`

interface EditAccountForm {
  AccessType: SelectOption<AccountAccessType>;
}

const defaultValues: EditAccountForm = {
  AccessType: { label: AccountAccessTypeLabels.get(AccountAccessType.Editor)!, value: AccountAccessType.Editor },
}

const accountAccessTypeOptions = [...AccountAccessTypeLabels.entries()].map(([accountAccessType, label]) => 
  ({ label: label, value: accountAccessType })
);

export interface EditAccountUserDialogProps {
  isOpen: boolean;
  accountUser: AccountUser;
  onClose: () => void;
  onSubmit: () => void;
}

function EditAccountUserDialog({ isOpen, onClose, onSubmit, accountUser }: EditAccountUserDialogProps) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))

  const methods = useForm<EditAccountForm>({
    defaultValues: {
      ...defaultValues,
      AccessType: { label: AccountAccessTypeLabels.get(accountUser.accessType)!, value: accountUser.accessType }
    },
    mode: 'onChange',
  });

  const {
    handleSubmit,
    control,
    formState: { isDirty },
    // watch,
    // getValues,
  } = methods;

  function onModalClose() {
    onClose();
  }

  function onModalOK() {
    onClose();
  }

  return (
    <Dialog
      fullScreen={fullScreen}
      open={isOpen}
      onClose={onModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        {accountUser.name}
      </DialogTitle>
      <DialogContent className={ContentStyle}>
        <form onSubmit={handleSubmit(() => {})}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <AutoCompleteField
                name="AccessType"
                disableClearable
                control={control}
                label="Access"
                options={accountAccessTypeOptions}
              />
            </Grid>
          </Grid>
        </form>
      </DialogContent>
      <DialogActions>
        <Button onClick={onModalClose}>
          Cancel
        </Button>
        <Button variant="contained" disabled={!isDirty} onClick={onModalOK}>
          Update
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default EditAccountUserDialog;
