import * as ColorHash from 'color-hash';

// const colorHash = new ColorHash({ lightness: [0.3, 0.45, 0.6] });
// const colorHash = new ColorHash({ lightness: [0.4, 0.4, 0.6] });
const colorHash = new ColorHash({ lightness: 0.45 });

export const stringToHslColor = (value: string, saturation: number, lightness: number) => {
  let hash = 0;
  for (var i = 0; i < value.length; i++) {
    hash = value.charCodeAt(i) + ((hash << 5) - hash);
    // hash = value.charCodeAt(i) + ((hash << 5) - hash);
  }
  
  const hex = hash % 360;
  return `hsl(${hex}, ${saturation}%, ${lightness}%)`;
}

export const avatarColor = (value: string) => colorHash.hex(value)

