import { Autocomplete, createFilterOptions, TextField } from '@material-ui/core';
import styled from '@emotion/styled';
import AssignableUser from 'types/AssignableUser';
import { css } from 'emotion';
import React from 'react';

const Name = styled.span`
  display: block !important;
  font-size: 14px;
  margin-bottom: 0.5em;
`;

const UserMatch = styled.span`
  display: block;
`;

const Email = styled.span`
  display: block;
  font-size: 11px;
  color: #666;
`;

const Mobile = styled.span`
  display: block;
  font-size: 11px;
  color: #666;
`;

const ListBoxStyles = css`
  .MuiAutocomplete-option {
    padding-top: 8px;
    padding-bottom: 8px;
  }
`;

const TextFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

const allUsers: AssignableUser[] = [
  {
    id: 3,
    name: 'Kim Wexler',
    email: 'kim.wexler@wm.com',
    mobile: '+61 444 555 666',
  },
  {
    id: 2,
    name: 'Saul Goodman',
    email: 'saul.goodman@itsallgoodman.com',
    mobile: '+61 432 109 876',
  },
  {
    id: 1,
    name: 'Walter White',
    email: 'walter.white@bb.com',
    mobile: '+61 456 789 012'
  }
];

const filter = createFilterOptions<AssignableUser>();

interface UserAssignFieldProps {
  value: AssignableUser | null;
  onChange: (user: AssignableUser | null) => void;
}

const UserAssignField = ({ value, onChange }: UserAssignFieldProps) => {
  return (
    <Autocomplete
      value={value}
      autoHighlight
      classes={{
        listbox: ListBoxStyles
      }}
      options={allUsers}
      getOptionSelected={(option: AssignableUser, value: AssignableUser) => { 
        return value ? option === value : false;
      }}
      renderOption={(props, value, state) => {
        return (
          value.id === 0 && !value.inputValue ? 
          <span>
            <UserMatch>
              <Email>{value.email}</Email>
            </UserMatch>
          </span> :
          <li {...props}>
            <UserMatch>
              <Name>{value.name}</Name>
              <Email>{value.email}</Email>
              {!!value.mobile && <Mobile>{value.mobile}</Mobile>}
            </UserMatch>
          </li>
        )
      }}
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      getOptionLabel={(option) => {
        // Value selected with enter, right from the input
        if (typeof option === 'string') {
          return option;
        }
        // Add "xxx" option created dynamically
        if (option.inputValue) {
          return option.inputValue;
        }
        // Regular option
        return `${option.name} ${option.email} ${option.mobile}`
      }}
      // getOptionLabel={(option) => `${option.name} ${option.email} ${option.mobile}`}
      // value={value}
      // value={selectedValueWithLabel}
      // onChange={(evt, newValue) => onChange(newValue)}
      onChange={(evt, newValue) => {
        if (typeof newValue === 'string') {
          onChange({
            id: 0,
            name: newValue,
            email: newValue,
          });
        } else if (newValue) {
          // Create a new value from the user input
          onChange(newValue);
        } else {
          onChange(null);
        }
      }}
      filterOptions={(options, params) => {
        const filtered = filter(options, params);

        // Suggest the creation of a new value
        if (params.inputValue !== '') {
          if (params.inputValue.indexOf('@') > -1) {
            filtered.push({
              inputValue: params.inputValue,
              id: 0,
              name: '',
              email: `Add "${params.inputValue}"`
            });
          // } else {
          //   filtered.push({
          //     // inputValue: params.inputValue,
          //     id: 0,
          //     name: '',
          //     email: 'Type email address to Add'
          //   });
          }
        }

        return filtered;
      }}
      // onBlur={onBlur}
      freeSolo
      renderInput={({ InputLabelProps, InputProps, ...rest }) => 
        <TextField
          autoFocus={true}
          className={TextFieldStyle}
          // label="Find or create user"
          // error={!!error}
          InputLabelProps={{ shrink: true, ...InputLabelProps }}
          InputProps={{ placeholder: 'Find or create user', ...InputProps }}
          // helperText={ error?.message ?? '' }
          {...rest}
        />
      }
    />
  )
};

export default UserAssignField;
