// import styled from '@emotion/styled';
import {
  Button,
  Chip,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  useMediaQuery,
  useTheme,
} from '@material-ui/core';
import AutoCompleteField, { SelectOption } from 'components/form/AutoCompleteField';
import CheckboxField from 'components/form/CheckBoxField';
import TextField from 'components/form/TextField';
import { css } from 'emotion';
import React from 'react';
import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { AccountAccessType, AccountAccessTypeLabels } from 'types/AccountAccessType';
import AssignableUser from 'types/AssignableUser';
import UserAssignField from './UserAssignField';

const ContentStyle = css`
  width: 350px;
  min-height: 175px;
`

const SelectedUserChipLabelStyle = css`
  font-size: 14px;
`

interface NewAccountUserForm {
  AccessType: SelectOption<AccountAccessType>;
  Message: string;
  GivenName: string;
  FamilyName: string;
  SendNotification: boolean;
}

const defaultValues: NewAccountUserForm = {
  AccessType: { label: AccountAccessTypeLabels.get(AccountAccessType.Editor)!, value: AccountAccessType.Editor },
  Message: '',
  SendNotification: false,
  GivenName: '',
  FamilyName: '',
}

const accountAccessTypeOptions = [...AccountAccessTypeLabels.entries()].map(([accountAccessType, label]) => 
  ({ label: label, value: accountAccessType })
);

export interface NewAccountUserDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (accountUser: NewAccountUserForm) => void;
}

function NewAccountUserDialog({ isOpen, onClose, onSubmit }: NewAccountUserDialogProps) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))
  const [selectedUser, setSelectedUser] = useState<AssignableUser | null>(null);

  const methods = useForm<NewAccountForm>({
    defaultValues,
    mode: 'onChange',
  });

  useEffect(
    () => {
      // Unmount CallBack
      if (!isOpen) {
        reset();
      }
      return () => reset();
    },
    [isOpen]
  )

  const {
    handleSubmit,
    control,
    watch,
    // getValues,
  } = methods;

  const sendNotification = watch('SendNotification') === true;

  function onModalClose() {
    onClose();
  }

  function reset() {
    setSelectedUser(null);
  }

  function onModalOK() {
    // onSubmit(getValues('FindAbn'));
  }

  function onSelectionRemove() {
    setSelectedUser(null);
  }
  
  return (
    <Dialog
      fullScreen={fullScreen}
      open={isOpen}
      onClose={onModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        New Account User
      </DialogTitle>
      <DialogContent className={ContentStyle}>
        <form onSubmit={handleSubmit(() => {})}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              {
                (selectedUser) ?
                <Chip
                  classes={{ label: SelectedUserChipLabelStyle }}
                  label={selectedUser.inputValue ? selectedUser.inputValue : selectedUser.name}
                  onDelete={onSelectionRemove}
                  // variant="outlined"
                />
                :
                <UserAssignField
                  value={selectedUser}
                  onChange={setSelectedUser}
                />
              }
            </Grid>

            {
              !!selectedUser &&
              <React.Fragment>
                {
                  !!selectedUser.inputValue &&
                  <Grid item container spacing={1}>
                    <Grid item xs={6}>
                      <TextField
                        autoFocus
                        name="GivenName"
                        label="First Name"
                        control={control}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        name="FamilyName"
                        label="Family Name"
                        control={control}
                      />
                    </Grid>
                  </Grid>
                }
                <Grid item xs={12}>
                  <AutoCompleteField
                    name="AccessType"
                    disableClearable
                    control={control}
                    label="Access"
                    options={accountAccessTypeOptions}
                  />
                </Grid>
                <Grid item xs={12}>
                  <CheckboxField
                    name="SendNotification"
                    control={control}
                    label="Notify"
                  />
                </Grid>
                {
                  sendNotification &&
                  <Grid item xs={12}>
                    <TextField
                      autoFocus
                      name="Message"
                      control={control}
                      multiline
                      minRows={3}
                      label="Message"
                    />
                </Grid>
                }
              </React.Fragment>
            }
          </Grid>
        </form>
      </DialogContent>
      <DialogActions>
        <Button onClick={onModalClose}>
          Cancel
        </Button>
        <Button variant="contained" onClick={onModalOK}>
          { sendNotification ? 'Send' : 'Assign'}
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default NewAccountUserDialog;
